<template>
  <div>
    <p>{{  execType }}</p>
    <v-card elevation="2">
      <v-row>
        <v-col cols="4">
            <v-img :src="book.image" width="200"></v-img>
        </v-col>
        <v-col cols="8">
          <v-text-field id="isbn" label="ISBN" outlined v-model="isbn" :readonly="editmode" @input="checkISBN"></v-text-field>
          <br>
          <v-text-field label="Title" outlined v-model="book.title"></v-text-field>
          <br>
          <v-text-field label="Authors" outlined v-model="book.authors"></v-text-field>
          <br>
          <p v-show="book_status">
          状態：{{ book_status }}  <br>
          </p>
          <v-btn rounded large color="primary" dark @click="updateBook">{{  execType }}</v-btn>
        </v-col>
      </v-row>
    </v-card>
    <common-dialog :dialog="dialog" @close-common-dialog="closeCommonDialog" />
</div>
</template>

<script>
import CommonDialog from '@/global/CommonDialog'

export default {
    name:'BookEdit',
    props:{
        books: Array,
        users: Array,
        borrowing: Array
    },
    components: {
      'common-dialog': CommonDialog
    },
    data(){
        return {
            book: { isbn:'', title:'', authors: '', image:'no-image.png' },
            editmode: false,
            isbn: '',
            execType: '',
            book_status: '',
            dialog: {on: false, msg: 'test text', type: 'notice', to: '' },
        }
    },
  methods:{
    updateBook() {
        console.log( this.book )
        if ( !this.book.isbn || !this.book.title ) {
          this.dialog.msg = 'ISBNとタイトルを入力して下さい。'
          this.dialog.on = true
          return
        }
      this.$emit('update-books', this.book )
      this.dialog.msg = this.execType + '処理を実行しました。'
      this.dialog.to = '/book_search'
      this.dialog.on = true
      return

    },
    resetForm() {
      this.book =  { isbn:'', title:'', authors: '', image:'no-image.png' }
      this.isbn =  ''      
    },
    closeDialog() {
      this.dialog.on = false
      let destination = this.dialog.to
      this.dialog.to = ''
      if (destination) this.$router.push(destination)
    },
    checkISBN() {
        if ( this.editmode || this.isbn.length !== 13 ) return
        let exist = this.books.some(b => b.isbn==this.isbn )
        if (exist) {
          this.dialog.msg = 'すでに登録済みです。'
          this.dialog.on = true
          return
        }
        let isbn = this.isbn
        console.log(isbn)
        this.isbn = ''

        this.getBookInfo(isbn)

//        this.$forceUpdate()

    },
    async getBookInfo(isbn) {
        console.log( "getBookInfo" )

        this.searchResults = []
        const baseUrl = 'https://www.googleapis.com/books/v1/volumes?'
        const params = {
            q: `isbn:${ isbn }`,
            maxResults:1
        }
        const queryParams = new URLSearchParams(params)
        const res = await fetch(baseUrl + queryParams)
          .then(res=>res.json())
        if (res.totalItems == 0) {
          this.dialog.msg = 'ISBN non found in Google Books'
          this.dialog.on = true
          return
        }
        console.log(res.items)
        const volumeInfo = res.items[0].volumeInfo
        console.log( volumeInfo )
        let title = (volumeInfo.title) ? volumeInfo.title : ''
        let authors = (Array.isArray( volumeInfo.authors ) ) ? volumeInfo.authors.toString() : '' 
        let image = (volumeInfo.imageLinks.thumbnail) ? volumeInfo.imageLinks.thumbnail : ''

        this.book.isbn = isbn
        this.book.title = title
        this.book.authors = authors
        this.book.image = image
    },
    closeCommonDialog(res) {
      this.dialog.on = false
      console.log('close common dialog: ' + res)
    },

  },

  mounted() {
      const isbn = this.$route.params.isbn
      if ( isbn == "0" ) {
        this.isbn = ''
        this.editmode = false
        this.execType = '登録'
        this.book_status = ''
      } else {
        console.log('isbn: ' + isbn)
        this.isbn = isbn
        this.editmode = true
        this.book = this.books.find((b) => b.isbn === isbn)
        console.log(this.book)
        this.execType = '更新'
        if ( this.borrowing.some((o)=>o.book_isbn === isbn) ) {
          this.book_status = '貸出中'
        } else {
          this.book_status = '在架'
        }
      }
  },

}
</script>

